.landing-support {
  .support-container {
    @media #{$extra-small-and-down} {
      width: 80%;
      margin: 0 auto !important;
    }
  }

  .support-title {
    background: url('../images/hero-support.png');
    background-color: black;
    background-size: cover;
  }

  @media (min-width: 1100px) {
    .support-title {
      background-position-y: -210px;
    }
  }

  .item {
    margin: 0 auto;
  }

  .button {
    margin: 0 auto;
  }
}
